const USDC_ADDRESS = "0x2791Bca1f2de4661ED88A30C99A7a9449Aa84174"

const CCOIN_ABI = IERC20.abi
const CCOIN_ADDRESS = "0x71b1E6b9e18519796Bdf204Ec4A54AB23b3BCd5b"

const CARBONSWAP_ABI = CarbonSwap.abi
const CARBONSWAP_ADDRESS = "0xe5d7E833909bF30c823006990060435a584d3250"

class App extends Component {
  async componentWillMount() {
    await this.init()
  }

  async init() {
    const provider = new ethers.providers.Web3Provider(window.ethereum)
    this.setState({ provider: provider})
    console.log(provider)
  }
  //connect button
  async on(event){    
    event.preventDefault()
    const provider = this.state.provider
    let accounts = await provider.send("eth_requestAccounts", [])
    let account = accounts[0]
    this.setState({ account })
    if (account !== 'undefined') {
      this.setState({ loading: false })
    }
    console.log(account)
    const accountSigned = provider.getSigner(account)
    this.setState({ accountSigned })
    console.log(this.state.accountSigned)

    // update account. sans balance
    provider.on("accountsChanged", async (accounts) => {
      
    })

    let token

    token = new ethers.Contract(USDC_ADDRESS, USDC_MAIN_ABI, accountSigned)
    this.setState({ token })
    console.log("signed token: ", token)

    let bal = await token.balanceOf(account)
    bal = ethers.utils.formatUnits(bal, 6)
    console.log("USDC balance: ", bal.toString())

    const ccoinToken = new ethers.Contract(CCOIN_ADDRESS, CCOIN_ABI, provider)
    this.setState({ ccoinToken })
    console.log("CCOIN: ", ccoinToken.address)

    const carbonSwap = new ethers.Contract(CARBONSWAP_ADDRESS, CARBONSWAP_ABI, accountSigned)
    this.setState({ carbonSwap })
    console.log("carbonswap: ", carbonSwap.address)

    this.refreshBalance()
  }

  buyTokens = async(tokenAmount) => {
    let txn, txhash

    window.alert('After you click the OK button, you will be taken to your wallet app where you will be prompted to approve 2 back to back transactions on your wallet. After you approve the first one, please wait until the second prompt appears and approve that too.')    
    this.setState({ loading: true })    
    tokenAmount = ethers.utils.parseUnits(tokenAmount.toString(), 6)
    console.log(tokenAmount)

    if(this.state.provider!==null){
      txn = await this.state.token.approve(this.state.carbonSwap.address, tokenAmount)
      txhash = txn.hash
      console.log(txhash)

      await this.state.provider.waitForTransaction(txhash).then(
        this.state.carbonSwap.swap(tokenAmount)
      )

      this.state.provider.on('error', (error) => {
        window.alert(error)
      })
      
      this.setState({ loading: false })
      
    } else {
      window.alert("Please click the CONNECT button to link your wallet first.")
      this.setState({ loading: false })
    }
  }

  async refreshBalance(event){
    let balance
    console.log(this.state.account)
    if(this.state.account!==null){
      console.log(this.state.ccoinToken)
      console.log(this.state.account)
      try {
        balance = await this.state.ccoinToken.balanceOf(this.state.account)
      } catch (e) {
        console.log(e)
      }
      console.log(balance)
      balance = balance.toString()
      balance = ethers.utils.formatUnits(balance, 18)
    } 
    
    this.setState({ balance: balance })
  }

  constructor(props) {
    super(props)
    this.state = {
      account: null,
      token: {},
      ccoinToken: {},
      carbonSwap: {},
      network: null,
      provider: null,
      loading: false,
      onlyNetwork: false,
      balance: 0,
    }

    this.on = this.on.bind(this)
  }

  render() {
    let content
    if(this.state.loading) {
      content = <p id="loader" className="text-center text-white"><br></br><br></br><br></br>Please wait... Processing your transaction...</p>
    } else {
      content = <Main
        account={this.state.account}
        loading={this.state.loading}
        network={this.state.network}
        onlyNetwork={this.state.onlyNetwork}
        buyTokens={this.buyTokens}
        balance={this.state.balance}
      />
    }

    return (
      <div>
        <Navbar
          on={this.on}
          account={this.state.account}
          balance={this.state.balance}
          loading={this.state.loading} 
        />
        <div className="container-fluid mt-5">
          <div className="row">
            <main role="main" className="col-lg-12 ml-auto mr-auto" style={{ maxWidth: '600px' }}>
              <div className="content mr-auto ml-auto">

                {content}

              </div>
            </main>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
